.selectable-chip-text {
  margin-bottom: 2px;
  font-family: "Abhaya Libre", serif;
  font-size: 24px;
}

.selectable-chip-text-roboto {
  margin-bottom: 2px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.selectable-chip {
  margin: 10px !important;
}

.accordian-summary {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.accordian-details {
  border-top: 1px dashed #acabac;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.accordian-root {
  margin-top: 30px;
  width: 95%;
}

.accordian-title {
  align-self: center;
}

.search-button {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}

.search-input {
  width: 100%;
}
