.header-bar {
  width: 100%;
  margin: 10px;
  background: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #dde0e4;
}

.header-name {
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  margin: 10px 0px 0px 0px;
}

.header-sinhala-name {
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  margin: 10px 0px 0px 0px;
  margin-bottom: 20px;
}

@media (min-width: 900px) {
}

@media (max-width: 768px) {
  .header-name {
    font-size: 20px;
  }
  .header-sinhala-name {
    font-size: 20px;
  }
}

@media (max-width: 600px) {
  .header-name {
    font-size: 18px;
  }
  .header-sinhala-name {
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .header-name {
    font-size: 16px;
  }
  .header-sinhala-name {
    font-size: 16px;
  }
}

@media (max-width: 320px) {
  .header-name {
    width: 90%;
    font-size: 16px;
  }
  .header-sinhala-name {
    width: 90%;
    font-size: 16px;
  }
}
