.container {
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #fdfdfd;
  min-height: 100vh;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  flex: 1;
  width: 70%;
  background: #fafbfc;
}

@media (min-width: 1024px) {
  .content {
    width: 90% !important;
  }
}

@media (max-width: 768px) {
  .content {
    width: 90% !important;
  }
}

@media (max-width: 600px) {
  .content {
    width: 90% !important;
  }
}

@media (max-width: 480px) {
  .content {
    width: 100% !important;
  }
}

@media (max-width: 320px) {
  .content {
    width: 100% !important;
  }
}
