.footer-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 60px;
  background: #363863;
}

.copyright-text {
  color: white;
  font-weight: 500;
  text-align: center;
  line-height: 25px;
  margin-left: 50px;
  margin-right: 50px;
}

.poweredby-text {
  color: white;
  font-weight: 500;
  text-align: center;
  line-height: 30px;
}

.img-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 0px 20px 0px;
}

.uni-logo {
  width: 8%;
  max-width: 100px;
  object-fit: contain;
}

.cdepd-logo {
  width: 20%;
  max-width: 300px;
  margin-left: 20px;
  object-fit: contain;
}

.link-color {
  color: #58a164;
  background-color: transparent;
  text-decoration: none;
}

@media (max-width: 600px) {
  .uni-logo {
    width: 20%;
  }

  .cdepd-logo {
    width: 40%;
  }
}

@media (max-width: 480px) {
  .uni-logo {
    width: 20%;
  }

  .cdepd-logo {
    width: 40%;
  }
}

@media (max-width: 320px) {
  .uni-logo {
    width: 30%;
  }

  .cdepd-logo {
    width: 50%;
  }
}
